</template>

<script>
export default {
  name: "deviceMange",
  data() {
    return {
      curIndex: 0,
      tabList: [
        {
          title: "所有订单",
          page: "",
          list: []
        },
        {
          title: "待付款",
          page: "",
          list: []
        },
        {
          title: "待发货",
          page: "",
          list: []
        },
        {
          title: "配送中",
          page: "",
          list: []
        }
      ]
    };
  },
  methods: {
    change(item, index) {
      this.curIndex = index;
    }
  }
};
