<style scoped lang="scss">
.active {
  color: #ff4c23 !important;
  border-bottom: 1px solid #ff4c23;
}
.device {
  nav {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    span {
      font-size: 13px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: #6b6b6b;
      line-height: 28px;
    }
  }
  main {
    .product {
      height: 100px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.05);
      border-radius: 6px;
      display: flex;
      align-items: center;
      margin-top: 10px;
      padding: 10px;
      img {
        width: 68px;
        height: 68px;
        border-radius: 5px;
      }
      .info_right {
        margin-left: 10px;
        flex: 1;
        .info_top {
          .info_title {
            font-size: 16px;
            font-family: SourceHanSansCN-Medium;
            font-weight: 500;
            color: rgba(36, 36, 36, 1);
            line-height: 28px;
          }
          .info_status {
            background: rgba(79, 207, 86, 1);
            border-radius: 12px;
            color: #fff;
            padding: 2px 10px;
            font-size: 13px;
          }
        }
        .info_bottom:after {
          content: "";
          position: absolute;
          right: 10px;
          margin-top: 3px;
          width: 14px;
          height: 12px;
          background: url(../../assets/images/mall_arrow@2x.png) no-repeat;
          background-size: 100% 100%;
        }
        .info_bottom {
          display: flex;
          justify-content: space-between;
          .info_content {
            font-size: 12px;
            font-family: SourceHanSansCN-Regular;
            font-weight: 400;
            color: rgba(90, 90, 90, 1);
            line-height: 21px;
          }
          .info_people {
            font-size: 12px;
            font-family: SourceHanSansCN-Regular;
            font-weight: 400;
            color: rgba(90, 90, 90, 1);
            line-height: 21px;
          }
        }
      }
    }
  }
  footer {
    width: 100%;
    padding: 17px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
    position: fixed;
    bottom: 0;
    text-align: center;
    font-size: 16px;
    font-family: SourceHanSansCN-Normal;
    font-weight: 400;
    color: rgba(255, 76, 35, 1);
    line-height: 26px;
  }
}
</style>



